import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { getBooks } from '../services/apiService';
import Modal from '../components/Modal';
import styles from '../styles/pages/Books.module.css';

const Books = () => {
  const [books, setBooks] = useState([]);
  const [selectedBook, setSelectedBook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { t } = useTranslation();

  useEffect(() => {
    const fetchBooks = async () => {
      try {
        const data = await getBooks();
        setBooks(data);
      } catch (error) {
        setError(t('books.errorFetchingBooks'));
      } finally {
        setLoading(false);
      }
    };

    fetchBooks();
  }, [t]);

  const closeModal = () => setSelectedBook(null);

  if (loading) return <div className="text-center text-gray-500 mt-5">{t('books.loading')}</div>;
  if (error) return <div className="text-center text-gray-500 mt-5"><p>{error}</p></div>;
  if (!books.length) return <div className="text-center text-gray-500 mt-5">{t('books.noResults')}</div>;

  return (
    <div className={styles.booksPage}>
      <h1 className="text-center text-2xl font-bold mb-6">{t('books.title')}</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {books.map((book) => (
          <div key={book.id} className={`${styles.bookCard} p-4 border rounded shadow-lg`}>
            <h2 className="text-xl font-semibold mb-4">{book.title}</h2>
            <p className="mb-4">{book.description}</p>
            <div className="flex justify-between">
              <button
                onClick={() => setSelectedBook(book)}
                className="bg-light-green text-black py-2 px-4 rounded"
              >
                {t('books.read')}
              </button>
              <a
                href={book.downloadLink}
                download
                className="bg-dark-green text-white py-2 px-4 rounded"
              >
                {t('books.download')}
              </a>
            </div>
          </div>
        ))}
      </div>

      {/* Modal for live reader */}
      <Modal show={!!selectedBook} onClose={closeModal}>
        {selectedBook && (
          <>
            <h2 className="text-xl font-semibold mb-4">{selectedBook.title}</h2>
            <iframe
              src={selectedBook.readLink}
              title={selectedBook.title}
              className="w-full h-full border rounded"
              style={{ minHeight: '600px' }}
            />
          </>
        )}
      </Modal>
    </div>
  );
};

export default Books;
