import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { getFatwas } from '../services/apiService';
import styles from '../styles/pages/FatwaEncyclopedia.module.css';
import { useTranslation } from 'react-i18next';

const FatwaEncyclopedia = () => {
  const [fatwas, setFatwas] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredFatwas, setFilteredFatwas] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const location = useLocation();
  const { t } = useTranslation();

  // Parse query parameters from the URL
  const params = new URLSearchParams(location.search);
  const category = params.get('category');
  const searchQuery = params.get('searchQuery');

  if (searchQuery) {
      setSearchTerm(searchQuery);
  }

  useEffect(() => {
    const fetchFatwas = async () => {
      try {
        // Fetch fatwas
        const params = {};
        if (category) {
          params.category = category;
        }
        if (searchQuery) {
          params.searchQuery = searchQuery;
        }
        const data = await getFatwas(params);
        setFatwas(data);
      } catch (error) {
        console.log(error);
        setError(t('fatwaEncyclopedia.errorFetchingFatwas'));
      } finally {
        setLoading(false);
      }
    };
    fetchFatwas();
  }, [location]);

  useEffect(() => {
    setFilteredFatwas(
      fatwas.filter(fatwa =>
        (
            fatwa.title.toLowerCase().includes(searchTerm.toLowerCase())
            ||
            fatwa.question.toLowerCase().includes(searchTerm.toLowerCase())
        )
      )
    );
  }, [searchTerm, fatwas]);

  if (loading) return <div className="text-center text-gray-500 mt-5">{t('fatwaEncyclopedia.loading')}</div>;
  if (error) return <div className="text-center text-gray-500 mt-5"><p>{error}</p></div>;
  if (!fatwas.length) return <div className="text-center text-gray-500 mt-5">{t('fatwaEncyclopedia.noResults')}</div>;

  return (
    <div className={`${styles.encyclopediaContainer} p-8`} style={{ direction: 'rtl' }}>
      <h1 className={`${styles.encyclopediaTitle} text-2xl font-bold mb-4`}>{t('fatwaEncyclopedia.title')}</h1>
      <input
        type="text"
        className={`${styles.encyclopediaSearch} border rounded p-2 mb-4 w-full`}
        placeholder={t('fatwaEncyclopedia.searchPlaceholder')}
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        style={{ textAlign: 'right' }}
      />
      <div className={`${styles.encyclopediaList} space-y-4`}>
        {filteredFatwas.map((fatwa, index) => (
          <div className={`${styles.encyclopediaItem} bg-white p-4 rounded-md shadow-md mb-4`} key={index}>
            <div className={styles.encyclopediaHeader}>
              <p className={styles.encyclopediaItemTitle}><strong>{fatwa.title}</strong></p>
              <div className={`${styles.encyclopediaItemInfo} text-gray-600`}>
                <span>{fatwa.date} | {t('fatwaEncyclopedia.refNo')}: {fatwa.refNo}</span>
              </div>
            </div>
            <p>{fatwa.question}</p>
            <Link to={`/fatwa/${fatwa.refNo}`} className="text-color-green hover:underline">
              {t('fatwaEncyclopedia.readMore')}
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
}

export default FatwaEncyclopedia;
