// MuftiList.js
import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { getMuftis } from '../services/apiService';

const MuftiList = ({ onSelectMufti }) => {
    const [muftis, setMuftis] = React.useState([]);
    const { t } = useTranslation();
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState(null);

    useEffect(() => {
        const fetchMuftis = async () => {
            try {
                const data = await getMuftis();
                setMuftis(data);
            } catch (error) {
                setError(t('muftiList.errorFetchingMuftis'));
            } finally {
                setLoading(false);
            }
        };
        fetchMuftis();
    }, []);

    if (loading) return <div className="text-center text-gray-500 mt-5">{t('muftiList.loading')}</div>;
    if (error) return <div className="text-center text-gray-500 mt-5"><p>{error}</p></div>;
    if (!muftis.length) return <div className="text-center text-gray-500 mt-5">{t('muftiList.noResults')}</div>;

    return (
        <div className="mufti-list max-w-xl mx-auto p-6 min-h-screen">
            <h2 className="text-2xl font-semibold mb-6 text-center">{t('muftiList.title')}</h2>
            <ul className="space-y-4">
                {muftis.map(mufti => (
                    <li key={mufti.id} className="bg-white p-4 rounded-lg shadow-md hover:bg-gray-100 transition">
                        <h3 className="text-xl font-medium">{mufti.name}</h3>
                        <p className="text-gray-600">{t('muftiList.specialty')}: {mufti.specialty}</p>
                        <p className="text-yellow-500">{t('muftiList.rating')}: {mufti.rating} ★</p>
                        <button
                            onClick={() => onSelectMufti(mufti)}
                            className="mt-4 bg-gradient-green-r text-white py-2 px-4 rounded hover:bg-green-600 transition"
                        >
                            {t('muftiList.bookCall')}
                        </button>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default MuftiList;
