import muftis from "./dummyMuftis";
import books from "./dummyBooks";
import dummyFatwas from "./dummyFatwas";


export const getFatwaByRefNo = async (refNo) => {
    // Dummy implementation for fetching fatwa by refNo
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));
    return dummyFatwas.find(fatwa => fatwa.refNo === refNo);
};

export const getFatwasByUserId = async (userId) => {
    // Dummy implementation for fetching fatwas by category
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));

    return dummyFatwas.filter(fatwa => fatwa.userId === userId);
};

export const getFatwas = async ({ category, searchQuery } = {}) => {
    // Dummy implementation for fetching all fatwas
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));

    let filteredFatwas = dummyFatwas;

    if (category) {
        filteredFatwas = filteredFatwas.filter(fatwa => fatwa.category === category);
    }

    if (searchQuery) {
        filteredFatwas = filteredFatwas.filter(fatwa =>
          fatwa.title.includes(searchQuery) || fatwa.question.includes(searchQuery)
        );
      }

    return filteredFatwas;
};

export const getFatwasCategories = async () => {
    // Dummy implementation for fetching all fatwas categories
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));

    const uniqueCategories = [...new Set(dummyFatwas.map(fatwa => fatwa.category))];
    return uniqueCategories;
};

export const searchFatwas = async (searchTerm) => {
    // Dummy implementation for fetching all fatwas categories
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));

    const filteredSuggestions = dummyFatwas
        .filter(fatwa => fatwa.title.toLowerCase().includes(searchTerm.toLowerCase()))
        .map(fatwa => fatwa.title);
    return filteredSuggestions;
};

export const getMuftis = async () => {
    // Dummy implementation for fetching all Muftis
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));
    return muftis;
};

export const updateMuftiAvailability = async (muftiId, selectedAvailability) => {
    try {
        // Dummy implementation for updating Mufti availability
        // Get rid of selectedAvailability
        muftis.forEach(mufti => {
            if (mufti.id === muftiId) {
                mufti.availability = mufti.availability.filter(availability => {
                    return availability !== selectedAvailability;
                });
                console.log(`Updated availability for Mufti with ID ${muftiId}`);
            }
        });
    } catch (error) {
        console.error('Error updating Mufti availability:', error);
    }
};

export const getBooks = async () => {
    // Dummy implementation for fetching books
    // Wait for 1 seconds
    await new Promise(resolve => setTimeout(resolve, 1000));
    return books;
  };
