import React, { useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import styles from '../styles/pages/BookingConfirmation.module.css';

const BookingConfirmation = ({ bookingDetails }) => {
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const meetingLinkInputRef = useRef(null);

    const copyToClipboard = () => {
        if (meetingLinkInputRef.current) {
            meetingLinkInputRef.current.select();
            document.execCommand('copy');
            alert(t('bookingConfirmation.copiedToClipboard'));
        }
    };

    const handleBackToHome = () => {
        navigate('/');
    };

    return (
        <div className={`${styles.bookingConfirmationPage} p-6 bg-gray-50 min-h-screen`}>
            <h2 className="text-2xl font-semibold mb-6 text-center">{t('bookingConfirmation.title')}</h2>
            <div className="max-w-lg mx-auto bg-white p-6 rounded-lg shadow-md">
                <p className="text-lg mb-4">{t('bookingConfirmation.thankYou', { muftiName: bookingDetails.mufti.name })}</p>
                <p className="font-semibold mb-2">{t('bookingConfirmation.yourDetails')}</p>
                <ul className="list-disc list-inside mb-4">
                    <li><strong>{t('bookingConfirmation.fullName')}:</strong> {bookingDetails.username}</li>
                    <li><strong>{t('bookingConfirmation.email')}:</strong> {bookingDetails.email}</li>
                    <li><strong>{t('bookingConfirmation.phone')}:</strong> {bookingDetails.phone}</li>
                    <li><strong>{t('bookingConfirmation.date')}:</strong> {bookingDetails.selectedDate ? bookingDetails.selectedDate.toLocaleDateString(i18n.language, { year: 'numeric', month: 'long', day: 'numeric' }) : ''}</li>
                    <li><strong>{t('bookingConfirmation.time')}:</strong> {bookingDetails.selectedTime}</li>
                    {bookingDetails.userMeetingLink && (
                    <li>
                        <strong>{t('bookingConfirmation.meetingLink')}:</strong>
                        <input
                            ref={meetingLinkInputRef}
                            type="text"
                            value={`${window.location.origin}${bookingDetails.userMeetingLink}`}
                            readOnly
                            onClick={copyToClipboard}
                            className={`${styles.meetingLinkInput} bg-light-green`}
                        />
                    </li>
                    )}
                </ul>
                <button
                    onClick={handleBackToHome}
                    className="w-full bg-gradient-green-r text-white py-2 px-4 rounded hover:bg-blue-600 transition"
                >
                    {t('bookingConfirmation.backToHome')}
                </button>
            </div>
        </div>
    );
};

export default BookingConfirmation;
