import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { loginUser } from '../redux/actions/authActions';
import { useTranslation } from 'react-i18next';
import styles from '../styles/pages/Login.module.css';

const Login = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [rememberMe, setRememberMe] = useState(false);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();

    const isRtl = i18n.language === 'ar';

    const handleLogin = (e) => {
        // Prevent the default form submission
        e.preventDefault();

        // Dispatch the login action
        dispatch(loginUser(username, password, rememberMe));

        // Redirect to home page after successful login
        navigate('/');
    };

    return (
        <div className={styles.loginContainer}>
            <div className='background-image-left-top-corner'></div>
            <div className={styles.loginBox}>
                <h2 className={styles.loginTitle}>{t('login.title')}</h2>
                <form onSubmit={handleLogin}>
                    <div className="input-group">
                        <input
                            type="text"
                            placeholder={t('login.usernamePlaceholder')}
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            required
                        />
                        <span className="icon"><i className="fas fa-user"></i></span>
                    </div>
                    <div className="input-group">
                        <input
                            type="password"
                            placeholder={t('login.passwordPlaceholder')}
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                        />
                        <span className="icon"><i className="fas fa-lock"></i></span>
                    </div>
                    <div className={`${styles.rememberMe} ${isRtl ? styles.rememberMeRtl : styles.rememberMeLtr}`}>
                        <label>
                            <input
                                type="checkbox"
                                checked={rememberMe}
                                onChange={() => setRememberMe(!rememberMe)}
                            />
                            {t('login.rememberMe')}
                        </label>
                    </div>
                    <button type="submit" className={`${styles.btnPrimary} bg-gradient-green-r text-button-white`}>
                        {t('login.submit')}
                    </button>
                </form>
                <p className={`${styles.createAccount} text-color-green`}>
                    <Link to="/signup">{t('login.createAccount')}</Link>
                </p>
            </div>
        </div>
    );
};

export default Login;
