import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { getFatwasByUserId } from '../services/apiService';
import styles from '../styles/pages/MyQuestions.module.css';

const MyQuestions = () => {
    const [questions, setQuestions] = useState([]);
    const [filteredQuestions, setFilteredQuestions] = useState([]);
    const { token, userId } = useSelector((state) => state.auth);
    const [ loading, setLoading ] = useState(true);
    const [ error, setError ] = useState(null);
    const { t } = useTranslation();
    const navigate = useNavigate();

    useEffect(() => {
        const fetchQuestions = async () => {
            try {
                if (!token) {
                    navigate("/login");
                }
                const userQuestions = await getFatwasByUserId(userId);
                setQuestions(userQuestions);

            } catch (error) {
                setError(t('myQuestions.errorFetchQuestions'));
            } finally {
                setLoading(false);
            }
        };

        fetchQuestions();

    }, [token, userId]);

    useEffect(() => {
        setFilteredQuestions(
            questions.map(fatwa => ({
                ...fatwa,
                status: fatwa.isAnswered ? t('myQuestions.answered') : t('myQuestions.unanswered')
            }))
        );
    }, [questions, t]);

    if (loading) return<div className="text-center text-gray-500 mt-5">{t('myQuestions.loadingQuestions')}</div>;
    if (error) return <div className="text-center text-red-500 mt-5">{error}</div>;
    if (questions.length === 0) return <div className="text-center text-gray-500 mt-5">{t('myQuestions.noResults')}</div>;

    return (
        <div className={`${styles.myQuestionsContainer} p-8`} style={{ direction: 'rtl' }}>
            <h1 className={`${styles.myQuestionsTitle} text-2xl font-bold mb-4`}>{t('myQuestions.title')}</h1>
            <div className={`${styles.myQuestionsList} space-y-4`}>
                {filteredQuestions.map((question, index) => (
                    <div className={`${styles.myQuestionsItem} bg-white p-4 rounded-md shadow-md mb-4`} key={index}>
                        <div className={styles.myQuestionsHeader}>
                            <p className={styles.myQuestionsItemTitle}><strong>{question.title}</strong></p>
                            <div className={`${styles.myQuestionsItemInfo} text-gray-600`}>
                                <span>{t('myQuestions.refNo')}: {question.refNo} | {t('myQuestions.status')}: {question.status}</span>
                            </div>
                        </div>
                        <p>{question.question}</p>
                        <Link to={`/fatwa/${question.refNo}`} className="text-color-green hover:underline">
                            {t('myQuestions.readMore')}
                        </Link>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default MyQuestions;
