import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { getFatwas, getFatwasCategories, searchFatwas } from '../services/apiService';
import '../styles/main.css'; // Import global styles
import FatwaChat from '../components/FatwaChat';
import styles from '../styles/pages/Home.module.css';

const Home = () => {
    const [fatwas, setFatwas] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [categories, setCategories] = useState([]);
    const [showDropdown, setShowDropdown] = useState(false);
    const [suggestions, setSuggestions] = useState([]); // State for autocomplete suggestions
    const [showSuggestions, setShowSuggestions] = useState(false); // State for suggestion visibility
    const [loadingFatwasCategories, setLoadingFatwasCategories] = useState(true);
    const [errorFetchingFatwasCategories, setErrorFetchingFatwasCategories] = useState(null);
    const [loadingFatwas, setLoadingFatwas] = useState(true);
    const [errorFetchingFatwas, setErrorFetchingFatwas] = useState(null);
    const navigate = useNavigate();
    const { t } = useTranslation();

    useEffect(() => {
        const fetchFatwas = async () => {
            try {
                // Fetch all fatwas
                const data = await getFatwas();

                // Sort Fatwas by date (most recent first)
                const sortedFatwas = data.sort((a, b) => new Date(b.date) - new Date(a.date));
                
                // Select the first three Fatwas
                const latestThreeFatwas = sortedFatwas.slice(0, 3);

                // Set the state with the latest three Fatwas
                setFatwas(latestThreeFatwas);
              } catch (error) {
                setErrorFetchingFatwas(t('home.errorFetchingFatwas'));
              } finally {
                setLoadingFatwas(false);
              }
        };

        fetchFatwas();
    }, []);

    useEffect(() => {
        // Fetch categories
        const fetchCategories = async () => {
            try {
                const data = await getFatwasCategories();
                setCategories(data);
            } catch (error) {
                setErrorFetchingFatwasCategories(t('home.errorFetchingFatwasCategories'));
            } finally {
                setLoadingFatwasCategories(false);
            }
        };
        fetchCategories();
    }, []);

    useEffect(() => {
        // Update suggestions based on search term
        if (searchTerm) {
            // Search for Fatwas based on search term
            const getSuggestions = async () => {
                const data = await searchFatwas(searchTerm);
                setSuggestions(data);
            };
            getSuggestions();
            setShowSuggestions(true);
        } else {
            setShowSuggestions(false);
        }
    }, [searchTerm]);

    const handleSubmitQuestionButtonClick = () => {
        navigate('/submit-question');
    };
    
    const handleSearchInputKeyPress = (event) => {
        if (event.key === 'Enter') {
            // Redirect to FatwaEncyclopedia with the search term as a query parameter
            navigate(`/encyclopedia?search=${searchTerm}`);
        }
    };
    
    const handleCategoryClick = (category) => {
        navigate(`/encyclopedia?category=${category}`);
    };
    
    const toggleDropdown = () => {
        setShowDropdown(!showDropdown);
    };

    // Handles suggestion click
    const handleSuggestionClick = (suggestion) => {
        setSearchTerm(suggestion);
        setShowSuggestions(false);
        navigate(`/encyclopedia?search=${suggestion}`);
    };

    const handleVideoCallButtonClick = () => {
        navigate('/video-call-booking');
    };

    return (
        <div className={styles.homePage}>
            <header className={`${styles.header} bg-gradient-green-l text-white text-center py-10`}>
                <h1 className="text-3xl">{t('home.header.quote')}</h1>
                <div className={`${styles.searchBar} mt-6 relative`}>
                    <input
                        type="text"
                        placeholder={t('home.search.placeholder')}
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        onKeyUp={handleSearchInputKeyPress}
                        className={`${styles.input} w-full max-w-lg px-4 py-3 rounded-md border-none text-gray-800`}
                    />
                    {showSuggestions && (
                        <div className={`${styles.suggestionsDropdown} absolute w-[43%] text-black mt-12 rounded-md shadow-lg z-50`}>
                            {suggestions.length > 0 ? (
                                suggestions.map((suggestion, index) => {
                                    const parts = suggestion.split(new RegExp(`(${searchTerm})`, 'gi'));
    
                                    return (
                                        <button
                                            key={index}
                                            onClick={() => handleSuggestionClick(suggestion)}
                                            className="block w-full px-4 pt-3 hover:bg-gray-200 rounded-md"
                                        >
                                            <div className="border-b border-gray-300 mx-2 pb-2">
                                                {parts.map((part, i) => (
                                                    <span
                                                        key={i}
                                                        className={
                                                            part.toLowerCase() === searchTerm.toLowerCase()
                                                                ? "text-green-700 font-semibold"
                                                                : ""
                                                        }
                                                    >
                                                        {part}
                                                    </span>
                                                ))}
                                            </div>
                                        </button>
                                    );
                                })
                            ) : (
                                <div className={`${styles.noSuggestions} px-4 pt-3 pb-2 rounded-md`}>
                                    {t('home.search.noSuggestions')}
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </header>
            <section className={styles.fatwaActions}>
                <button
                    className={`${styles.fatwaAction} bg-gradient-green-r text-button-white py-2 px-6 rounded-lg`}
                    onClick={handleVideoCallButtonClick}
                >
                    {t('home.fatwaActions.bookCall')}
                </button>
                <button
                    className={`${styles.fatwaAction} bg-gradient-green-r text-button-white py-2 px-6 rounded-lg`}
                    onClick={handleSubmitQuestionButtonClick}
                >
                    {t('home.fatwaActions.askQuestion')}
                </button>
                <div
                    className={`${styles.fatwaAction} bg-gradient-green-r text-button-white relative flex justify-center items-start hover:cursor-pointer`}
                    onClick={toggleDropdown}
                >
                    <button className="text-white items-center">
                        {t('home.fatwaActions.category')}
                    </button>
                    {showDropdown && (
                        <div className={`${styles.categoriesDropdown} absolute right-0 mt-2 w-full rounded-md shadow-lg ring-1 ring-black ring-opacity-5`}>
                            <div className="py-1">
                                {loadingFatwasCategories ? <div className='block text-center text-black'>{ t('home.loadingFatwasCategories') }</div> : (
                                    errorFetchingFatwasCategories ? <div className='block text-center text-black'>{ t('home.errorFetchingFatwasCategories') }</div> : (
                                        categories.map((category) => (
                                            <button
                                                key={category}
                                                onClick={() => handleCategoryClick(category)}
                                                className="block hover:bg-gray-200"
                                            >
                                                {category}
                                            </button>
                                        ))
                                    )
                                )}
                            </div>
                        </div>
                    )}
                </div>
            </section>
    
            <div className={`${styles.recentFatwasSection} flex justify-between px-4`}>
                <section className={`${styles.recentFatwas} flex-1`}>
                    {loadingFatwas ? <div className='bg-white p-4 rounded-md shadow-md mb-4 w-[80%] text-center'>{ t('home.loadingFatwas') }</div> : (
                        errorFetchingFatwas ? <div className='bg-white p-4 rounded-md shadow-md mb-4 w-[80%] text-center'>{ t('home.errorFetchingFatwas') }</div> : (
                            fatwas.map((fatwa, index) => (
                                <div className={`${styles.fatwaItem} bg-white p-4 rounded-md shadow-md mb-4`} key={index}>
                                    <div className={styles.fatwaHeader}>
                                        <p className={styles.fatwaTitle}>
                                            <strong>{fatwa.title}</strong>
                                        </p>
                                        <div className={styles.fatwaInfo}>
                                            <span className="text-gray-600">
                                                {fatwa.date} | {t('home.fatwaItem.refNo')} {fatwa.refNo}
                                            </span>
                                        </div>
                                    </div>
                                    <p>{fatwa.question}</p>
                                    <Link to={`/fatwa/${fatwa.refNo}`} className="text-color-green hover:underline">
                                        {t('home.fatwaItem.readMore')}
                                    </Link>
                                </div>
                            ))
                        )
                    )}
                </section>
                <section className={`${styles.categories} w-1/4`}>
                    {/* The dropdown menu is now part of the above section */}
                </section>
            </div>
            <FatwaChat />
        </div>
    );
    
    
    
    
};

export default Home;
