import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import DatePicker from 'react-datepicker';
import { registerLocale } from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import { enUS, fr, arSA } from 'date-fns/locale';
import { updateMuftiAvailability } from '../services/apiService';

// Register locales
registerLocale('en', enUS);
registerLocale('fr', fr);
registerLocale('ar', arSA);

const BookingForm = ({ mufti, onConfirm }) => {
    const { t, i18n } = useTranslation();
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [selectedDate, setSelectedDate] = useState(null);
    const [availableTimes, setAvailableTimes] = useState([]);
    const [selectedTime, setSelectedTime] = useState('');

    // Filter available times based on selected date
    useEffect(() => {
        if (selectedDate && mufti.availability) {
            // Get day of the week (0 = Sunday, 6 = Saturday)
            const dayOfWeek = selectedDate.getDay();

            // Mapping JavaScript days to Arabic names of days
            const daysMap = ["الأحد", "الإثنين", "الثلاثاء", "الأربعاء", "الخميس", "الجمعة", "السبت"];

            // Find the availability for the selected day
            const timesForDay = mufti.availability.find(avail => avail.day === daysMap[dayOfWeek]);

            // Set available times for that day
            setAvailableTimes(timesForDay ? [timesForDay.time] : []);
        } else {
            setAvailableTimes([]);
        }
    }, [selectedDate, mufti.availability]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        // Generate a unique meeting ID
        const meetingId = `meeting-${Date.now()}`;

        // Call the server to generate secure meeting links one for
        // the user and one for the Mufti
        // and send the Mufti meeting link to the Mufti
        // const response = await fetch('/api/generate-meeting-links', {
        //     method: 'POST',
        //     headers: { 'Content-Type': 'application/json' },
        //     body: JSON.stringify({ meetingId, username, muftiName: mufti.name })
        // });

        // const { userMeetingLink } = await response.json();
        
        // for now we will use the links without jwt token
        const userMeetingLink = `/meeting/${meetingId}`;

        // Prepare data
        const bookingDetails = {
            username,
            email,
            phone,
            selectedDate,
            selectedTime,
            mufti,
            userMeetingLink,
        };

        // Find the specific availability slot to remove
        const selectedAvailability = mufti.availability.find(avail => {
            return (
                avail.day === selectedDate.toLocaleDateString('ar', { weekday: 'long' }) &&
                avail.time === selectedTime
            );
        });

        // Update Mufti availability
        if (selectedAvailability) {
            await updateMuftiAvailability(mufti.id, selectedAvailability);
        }

        // Pass booking details to onConfirm
        onConfirm(bookingDetails);
    };

    return (
        <div className="booking-form p-6 bg-gray-50 min-h-screen">
            <h2 className="text-2xl font-semibold mb-6 text-center">
                {t('bookingForm.title', { muftiName: mufti.name })}
            </h2>
            <form onSubmit={handleSubmit} className="max-w-lg mx-auto bg-white p-6 rounded-lg shadow-md">
                <div className="space-y-4">
                    <div className="input-group">
                        <label className="block text-gray-700">{t('bookingForm.fullName')}</label>
                        <input
                            type="text"
                            placeholder={t('bookingForm.fullNamePlaceholder')}
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            required
                            className="w-full p-2 border rounded"
                        />
                    </div>
                    <div className="input-group">
                        <label className="block text-gray-700">{t('bookingForm.email')}</label>
                        <input
                            type="email"
                            placeholder={t('bookingForm.emailPlaceholder')}
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            required
                            className="w-full p-2 border rounded"
                        />
                    </div>
                    <div className="input-group">
                        <label className="block text-gray-700">{t('bookingForm.phone')}</label>
                        <input
                            type="text"
                            placeholder={t('bookingForm.phonePlaceholder')}
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                            className="w-full p-2 border rounded"
                        />
                    </div>
                    <div className="input-group">
                        <label className="block text-gray-700">{t('bookingForm.date')}</label>
                        <DatePicker
                            selected={selectedDate}
                            onChange={(date) => setSelectedDate(date)}
                            dateFormat="P"
                            locale={i18n.language === 'ar' ? 'ar' : 'en'}
                            placeholderText={t('bookingForm.selectDate')}
                            required
                            className="w-full p-2 border rounded"
                        />
                    </div>
                    {availableTimes.length > 0 && (
                        <div className="input-group">
                            <label className="block text-gray-700">{t('bookingForm.time')}</label>
                            <select
                                value={selectedTime}
                                onChange={(e) => setSelectedTime(e.target.value)}
                                required
                                className="w-full p-2 border rounded"
                            >
                                <option value="">{t('bookingForm.selectTime')}</option>
                                {availableTimes.map((time, index) => (
                                    <option key={index} value={time}>
                                        {time}
                                    </option>
                                ))}
                            </select>
                        </div>
                    )}
                    {availableTimes.length === 0 && selectedDate && (
                        <p className="text-red-500 text-center">{t('bookingForm.noTimesAvailable')}</p>
                    )}
                    <button
                        type="submit"
                        className="w-full bg-gradient-green-r text-white py-2 px-4 rounded hover:bg-green-600 transition"
                    >
                        {t('bookingForm.confirmBooking')}
                    </button>
                </div>
            </form>
        </div>
    );
};

export default BookingForm;
