import React from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import styles from '../styles/pages/Meeting.module.css';

const Meeting = () => {
    const { meetingId } = useParams();
    const { t } = useTranslation();

    // Construct the Jitsi meeting URL using the meetingId
    const jitsiURL = `https://meet.jit.si/${meetingId}`;

    return (
        <div className={styles.meetingContainer}>
            <h2 className="text-2xl font-semibold text-center mb-6">{ t("meetingPage.meetingTitle") }</h2>
            <div className={styles.meetingIframeContainer}>
                <iframe
                    src={jitsiURL}
                    style={{ width: '100%', height: '100%', border: 'none' }}
                    allow="camera; microphone; fullscreen; display-capture"
                    title={ t("meetingPage.meetingTitle") }
                ></iframe>
            </div>
        </div>
    );
};

export default Meeting;
